import React from 'react';
import { Switch, Route, withRouter, Redirect } from 'react-router-dom'
import ParentCounterContainer from '../../containers/ParentCounterContainer';
import AboutContainer from '../../containers/AboutContainer';
import HomeContainer from '../../containers/HomeContainer';
import ErrorContainer from '../../containers/ErrorContainer';
import HeaderContainer from '../../containers/HeaderContainer';
import LoginContainer from '../../containers/LoginContainer';

function withRoot(Component) { 
    return class includeHeaderComponent extends React.Component { 
        render() { 
            return ( <HeaderContainer> <Component/> </HeaderContainer> ); 
        } 
    }; 
}

function main() {
    return (
        <Switch>
            <Route exact path='/reactgh/' component={withRoot(HomeContainer)} />
            <Route path='/reactgh/counters' component={withRoot(ParentCounterContainer)}/>
            <Route path='/reactgh/about' component={withRoot(AboutContainer)} />
            <Route path='/reactgh/login' component={withRoot(LoginContainer)} />
            <Route path='/reactgh/404' component={ErrorContainer}/>
            <Redirect to='/reactgh/404' />
        </Switch>
    )
}
export default withRouter(main);



